          <div class="character-form">
            <label for="genshinCharacter">Выберите имя персонажа:</label>
            <select v-model="newCharacter.name" id="genshinCharacter">
              <option v-for="character in genshinCharacters" :key="character.id" :value="character.name">
                {{ character.name }}
              </option>
            </select>
          <div v-if="newCharacter.name">
<!--            <h3>Выбранный персонаж: {{ newCharacter.name }}</h3>-->
            <div>
              <div class="level-control">
              <label for="firstLevelRange">Уровень 1:</label>
              <input type="range" min="1" max="90" v-model="newCharacter.firstLevel" id="firstLevelRange" class="level-range">
              <span>{{ newCharacter.firstLevel }}</span>
              </div>
            </div>
            <div>
              <label for="secondLevelRange">Уровень 2:</label>
              <input type="range" min="1" max="90" v-model="newCharacter.secondLevel" id="secondLevelRange" class="level-range">
              <span>{{ newCharacter.secondLevel }}</span>
            </div>
            <div>
              <label for="thirdLevelRange">Уровень 3:</label>
              <input type="range" min="1" max="90" v-model="newCharacter.thirdLevel" id="thirdLevelRange" class="level-range">
              <span>{{ newCharacter.thirdLevel }}</span>
            </div>
            <button @click="addCharacter">Добавить</button>
          </div>
        </div>
        </div>
      </div>
      <table v-if="characters.length > 0">
        <tbody class="pers">
        <tr v-for="(character, index) in characters" :key="index" :class="{ 'favorite': character.favorite }">
          <td>
          <img :src="character.imagePath" :alt="character.name" style="width: 40%; height: 40%;">
          </td>
          <td>
            <tr>
              {{ character.name }}
              <button @click="toggleFavorite(character)"><img class="img"  src="https://icon-library.com/images/like-png-icon/like-png-icon-26.jpg" alt="About"></button>
            </tr>
            <tr>
              <div class="level-up-days">
                <p>Дни повышения уровня:</p>
                <div v-for="day in character.levelUpDays" :key="day">
                  {{ day }}
                </div>
              </div>
            </tr>
          </td>
          <td>
          <tr>
            Нормальная атака: {{ character.firstLevel }}
            <div class="level-control">
              <button @click="decreaseLevel(character, 'firstLevel')" class="level-button_1"><</button>
            <div class="level-bar-container">
              <div class="level-bar_1" :style="{ width: (character.firstLevel * 1.11) + '%' }"></div>
            </div>
              <button @click="increaseLevel(character, 'firstLevel')" class="level-button_1">></button>
            </div>
          </tr>
          <tr>
            Элементальный навык: {{ character.secondLevel }}
            <div class="level-control">
              <button @click="decreaseLevel(character, 'secondLevel')" class="level-button_2"><</button>
              <div class="level-bar-container">
                <div class="level-bar_2" :style="{ width: (character.secondLevel * 1.11) + '%' }"></div>
              </div>
              <button @click="increaseLevel(character, 'secondLevel')" class="level-button_2">></button>
            </div>
          </tr>
          <tr>
            Взрыв стихий: {{ character.thirdLevel }}
            <div class="level-control">
              <button @click="decreaseLevel(character, 'thirdLevel')" class="level-button_3"><</button>
              <div class="level-bar-container">
                <div class="level-bar_3" :style="{ width: (character.thirdLevel * 1.11) + '%' }"></div>
              </div>
              <button @click="increaseLevel(character, 'thirdLevel')" class="level-button_3">></button>
            </div>
          </tr>
          </td>
        </tr>
