<script>
export default {
  name: "Main",
  data() {
    return {
      selectedCharacter: '',
      firstLevel: 1,
      secondLevel: 1,
      thirdLevel: 1,
      newCharacter: {
        name: '',
        firstLevel: 1,
        secondLevel: 1,
        thirdLevel: 1
      },
      characters: [],
      showForm: false,
      genshinCharacters: [
        { id: 1, name: 'Amber', imagePath:'https://w0.peakpx.com/wallpaper/1/448/HD-wallpaper-video-game-genshin-impact-amber-genshin-impact.jpg', levelUpDays: 'ПН ВТ ВС'},
        { id: 2, name: 'Barbara', imagePath:'https://w.forfun.com/fetch/2d/2d808e12eff0a821ac8fb1c7b70d5ccf.jpeg'},
        { id: 3, name: 'Beidou', imagePath:'https://images8.alphacoders.com/110/1109824.png'},
        { id: 4, name: 'Bennett', imagePath:'https://w.forfun.com/fetch/d3/d33f333e466fc048944d056a56bff2af.jpeg'},
        { id: 5, name: 'Chongyun', imagePath:'https://cdn.mos.cms.futurecdn.net/RcA6MYmVUUdJSbLzUpf5XY.png'},
        { id: 6, name: 'Diluc', imagePath:'https://w.forfun.com/fetch/9a/9a11699fe9b884606f08f59abcdc08d1.jpeg'},
        { id: 7, name: 'Diona', imagePath:'https://volsiz.ru/wp-content/uploads/2022/08/weacrmayfv3dlhkyzukdab.jpg'},
        { id: 8, name: 'Eula', imagePath:'https://volsiz.ru/wp-content/uploads/2022/08/zvxz5hwytxwm6m8ogqwthc.jpg'},
        { id: 9, name: 'Fischl', imagePath:'https://rerollcdn.com/GENSHIN/Characters/1/Fischl.png'},
        { id: 10, name: 'Ganyu', imagePath:'https://i1.wp.com/traveler.gg/wp-content/uploads/Ganyu-Portrait.png?fit=2154%2C1320&ssl=1'},
        { id: 11, name: 'Hu Tao', imagePath:'https://w.forfun.com/fetch/30/300baf40bdb2bfe313364a07ca32dad0.jpeg'},
        { id: 12, name: 'Jean', imagePath:'https://imagesvc.meredithcorp.io/v3/mm/image?url=https://www.pockettactics.com/wp-content/uploads/2020/09/genshin-impact-tier-list-2.jpg'},
        { id: 13, name: 'Kaeya', imagePath:'https://i.pinimg.com/originals/26/5b/4c/265b4c3cd73695ee17f8a5d91c86ee3a.jpg'},
        { id: 14, name: 'Klee', imagePath:'https://i.pinimg.com/originals/da/9a/30/da9a30bb1ec0b4c432f835b95ee6bdd2.jpg'},
        { id: 15, name: 'Lisa', imagePath:'https://mundogenshinimpact.com/wp-content/uploads/Gesnhin-Impact-Lisa.png'},
        { id: 16, name: 'Mona', imagePath:'https://i.playground.ru/i/pix/3089906/image.jpg'},
        { id: 17, name: 'Ningguang', imagePath:'https://i.pinimg.com/originals/0e/66/ab/0e66abd9206b3c65a26503853ee68db0.png'},
        { id: 18, name: 'Noelle', imagePath:'https://wallpapercave.com/wp/wp8505426.png'},
        { id: 19, name: 'Qiqi', imagePath:'https://besthqwallpapers.com/Uploads/19-1-2021/152617/qiqi-artwork-genshin-impact-kaeya-alberich-ghosts.jpg'},
        { id: 20, name: 'Razor', imagePath:'https://avatars.mds.yandex.net/get-mpic/5234219/img_id3919635833406184053.jpeg/orig'}
      ]
    };
  },
  methods: {
    addCharacter() {
      if (this.newCharacter.name) {
        const selectedCharacter = this.genshinCharacters.find(char => char.name === this.newCharacter.name);
        if (selectedCharacter) {
          this.characters.push({
            name: this.newCharacter.name,
            firstLevel: this.newCharacter.firstLevel,
            secondLevel: this.newCharacter.secondLevel,
            thirdLevel: this.newCharacter.thirdLevel,
            imagePath: selectedCharacter.imagePath,
            levelUpDays: this.newCharacter.levelUpDays,
          });
          this.resetForm();
          this.toggleForm(); // Скрыть форму после добавления
        }
        if (this.name && this.firstLevel && this.secondLevel && this.thirdLevel) {
          this.addPointRequest(this.name, this.firstLevel, this.secondLevel, this.thirdLevel);
        }
      }
    },
    resetForm() {
      this.newCharacter = {
        name: '',
        firstLevel: 1,
        secondLevel: 1,
        thirdLevel: 1
      };
    },
    toggleForm() {
      this.showForm = !this.showForm; // Показать/скрыть форму
    },
    getCharacterImage(characterName) {
      // Используем путь к изображению, хранимый в genshinCharacters
      const selectedCharacter = this.genshinCharacters.find(char => char.name === characterName);
      if (selectedCharacter) {
        return selectedCharacter.imagePath;
      }
      // Вернуть путь к заглушке, если путь к изображению не найден
      return '/placeholder-image.jpg';
    },
    increaseLevel(character, levelType) {
      if (character[levelType] < 90) {
        character[levelType]++;
      }
    },
    decreaseLevel(character, levelType) {
      if (character[levelType] > 1) {
        character[levelType]--;
      }
    },
    toggleFavorite(character) {
      character.favorite = !character.favorite;

      if (character.favorite) {
        // Если персонаж добавлен в избранное, переместим его в начало массива
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.unshift(character);
        }
      } else {
        // Если персонаж удален из избранного, переместим его обратно в изначальное положение в массиве
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.push(character);
        }
      }
    },
    logoutRequest: function () {
      this.$axios({
        method: 'post',
        url: this.$BaseURL + 'logout',
      }).then(response => {
        this.response = response;
        localStorage.removeItem('currentUser');
        this.$router.push('/');
      }).catch(error => {
        console.log(error);
      });
    },
    getCharactersRequest: function () {
      let base64Credential = localStorage.getItem('currentUser');
      return this.$axios({
        method: 'get',
        url: this.$BaseURL + 'points',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        }
      }).then(response => {
        if (response.data.length) {
          this.characters.splice(0, this.characters.length, ...response.data);
        }
      }).catch(error => {
        console.log(error.response.data);
      });
    },
    addPointRequest: function (name, firstLevel, secondLevel, thirdLevel) {
      let base64Credential = localStorage.getItem('currentUser');
      return this.$axios({
        method: 'post',
        url: this.$BaseURL + 'points',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        },
        data: {name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel}
      }).then(response => {
        let result = response.data.result;
        this.characters.unshift({name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel, result: result});
        this.response = response;
      }).catch(error => {
        console.log(error.response.data);
        return false;
      });
    },































    mounted() {
      // При загрузке страницы получаем от сервера массив точек и добавляем его в data
      this.getCharactersRequest().then(() => {
        this.$nextTick(() => {
          // this.drawGraph();
        });
      });
    }
  }
}
</script>

